<template>

    <div class="app">
        <div class="art">
            <p>这里的一级会员指的是“等级”尚在一级的会员。</p>
            <p>例如:A升级到2级，那么A就会变动到您的二级会员之内。</p>
            <p>以此类推，所以请及时提醒等级较低的用户，完成升级任务。</p>
            <div>您的下级会员共计：{{teamData.user_count}}人</div>
        </div>
        <ul class="listUl">
            <li v-for="(item, key) in teamData.users">
                <div class="listMember" @click="getShow(key)" ><i></i><a href="javascript:void(0);">{{SectionToChinese(key)}}级会员</a><p>（{{item.length}}人）</p><span></span></div>
                <div class="conUlList" v-show="showkey == key">
                    <p class="conTitle"><span>注册时间</span><span>微信二维码</span></p>
                    <div class="bodyList">
                        <p v-for="val in item"><span>{{val.register_time}}</span><img :src="val.wechat_code" alt=""></p>
                    </div>
                </div>
            </li>
        </ul>
        <!--  公共组件  -->
        <tree-bar :tree_id="tree_id"></tree-bar>
    </div>

</template>

<script>

    import treeBar from '@/page/tree/treeBar'
    import { treeTeam } from '@/api/tree'
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';

    export default {
        name: "Treeinvite",
        components: {
            treeBar
        },
        data()
        {
            return {
                tree_id:'',
                teamData:{},
                showkey:''
            }
        },
        created()
        {
            this.tree_id = this.$route.query.treeId
        },
        mounted()
        {
            this.getList()
        },
        methods:{

            // 我的财富树列表
            getList() {
                treeTeam({tree_id: this.tree_id}).then(res => {

                    this.teamData = res
                })
            },
            getShow(key)
            {
               if (this.showkey == key)
               {
                   this.showkey = ''

               } else
               {
                   if (this.teamData.users[key].length > 0)
                   {
                       this.showkey = key
                   }

               }
            },
            SectionToChinese(section) {
                var chnNumChar = ["零", "一", "二", "三", "四", "五", "六", "七", "八", "九"];
                var chnUnitSection = ["", "万", "亿", "万亿", "亿亿"];
                var chnUnitChar = ["", "十", "百", "千"];
                var strIns = '', chnStr = '';
                var unitPos = 0;
                var zero = true;
                while (section > 0) {
                    var v = section % 10;
                    if (v === 0) {
                        if (!zero) {
                            zero = true;
                            chnStr = chnNumChar[v] + chnStr;
                        }
                    } else {
                        zero = false;
                        strIns = chnNumChar[v];
                        strIns += chnUnitChar[unitPos];
                        chnStr = strIns + chnStr;
                    }
                    unitPos++;
                    section = Math.floor(section / 10);
                }
                let str = chnStr.replace(/一十/, '十')
                return str
            }
        }
    }
</script>

<style scoped>
    @import "../../static/css/treeteam.css";
</style>



